/**
    Create logo
*/
@function create-svg-logo($logo, $logo-type) {
  $logo: '<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="#{view-box($logo-type)}">#{$logo}</svg>';
  @return $logo;
}

@function logo-type($logo-name) {
  $logoType: '';
  @if $logo-name == thinky-full-logo {
    $logoType: full;
  } @else if $logo-name == thinky-logo {
    $logoType: logo;
  } @else if $logo-name == thinky-circle {
    $logoType: circle;
  } @else if $logo-name == thinky-slogan {
    $logoType: slogan;
  } @else if $logo-name == fathym-logo {
    $logoType: words;
  } @else if $logo-name == IDE-logo {
    $logoType: ide;
  }
  @return $logoType;
}

/**
    container size for the image,
    this needs to be changed based off image size
  */
  @function view-box($logo-type) {
    $viewBox: '';
    @if $logo-type == logo {
        $viewBox: $thinkyLogo-viewBox;
    } @else if $logo-type == full {
        $viewBox: $thinkyFull-viewBox;
    } @else if $logo-type == circle {
        $viewBox: $thinkyCircle-viewBox;
    } @else if $logo-type == slogan {
        $viewBox: $thinkySlogan-viewBox;
    } @else if $logo-type == words {
        $viewBox: $fathymLogo-viewBox;
    } @else if $logo-type == ide {
        $viewBox: $IDELogo-viewBox;
    }
    @return $viewBox;
  }
  /**
    String replace utility
  */
  @function str-replace($string, $search, $replace: '') {
    $index: str-index($string, $search);

    @if $index {
      @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
    }

    @return $string;
  }

